:root {
  --pri-font:"Rubik", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --mod-blue: hsl(238, 40%, 52%);
  --soft-red: hsl(358, 79%, 66%);
  --light-grey-blue: hsl(239, 57%, 85%);
  --pale-red: hsl(357, 100%, 86%);
  --dark-blue: hsl(212, 24%, 26%);
  --grey-blue: hsl(211, 10%, 45%);
  --light-grey: hsl(223, 19%, 93%);
  --v-light-grey: hsl(228, 33%, 97%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
  background-color: var(--light-grey);
}
body.no-scroll {
  overflow: hidden !important;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.allCommentCtn {
  width: min(92%, 46.875rem);
  margin-inline: auto;
}
.allCommentCtn .commentSection {
  width: 100%;
  margin-top: 1.5rem;
}
.allCommentCtn .commentSection:first-child {
  margin-top: 2.5rem;
}
.allCommentCtn .commentSection .commentCard__header .postedAt {
  color: var(--grey-blue);
}
.allCommentCtn .commentSection .commentCard__header .you {
  background-color: var(--mod-blue);
}
.allCommentCtn .commentSection .commentCard__comment {
  color: var(--grey-blue);
}
.allCommentCtn .commentSection .commentCard__footer .score {
  background-color: var(--light-grey) !important;
}
.allCommentCtn .commentSection .commentCard__footer .score i {
  font-weight: 700 !important;
  -webkit-text-stroke: 0.1rem;
  color: var(--light-grey-blue);
  cursor: pointer;
  transition: color 0.2s ease-in-out !important;
}
.allCommentCtn .commentSection .commentCard__footer .score i:hover {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .commentCard__footer .score .count {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .commentCard__footer .replyBtn {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .commentCard__footer .crudBtns {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .commentCard__footer .crudBtns .edit, .allCommentCtn .commentSection .commentCard__footer .crudBtns .delete {
  transition: opacity 0.2s ease-in-out !important;
}
.allCommentCtn .commentSection .commentCard__footer .crudBtns .edit:hover, .allCommentCtn .commentSection .commentCard__footer .crudBtns .delete:hover {
  opacity: 0.5;
}
.allCommentCtn .commentSection .replyCard {
  margin-top: 1.5rem;
  margin-left: auto;
}
.allCommentCtn .commentSection .replyCard__header .postedAt {
  color: var(--grey-blue);
}
.allCommentCtn .commentSection .replyCard__header .you {
  background-color: var(--mod-blue);
}
.allCommentCtn .commentSection .replyCard__comment {
  color: var(--grey-blue);
}
.allCommentCtn .commentSection .replyCard__footer .score {
  background-color: var(--light-grey) !important;
}
.allCommentCtn .commentSection .replyCard__footer .score i {
  font-weight: 700 !important;
  -webkit-text-stroke: 0.1rem;
  color: var(--light-grey-blue);
  cursor: pointer;
  transition: color 0.2s ease-in-out !important;
}
.allCommentCtn .commentSection .replyCard__footer .score i:hover {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .replyCard__footer .score .count {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .replyCard__footer .replyBtn {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .replyCard__footer .crudBtns {
  color: var(--mod-blue);
}
.allCommentCtn .commentSection .replyCard__footer .crudBtns .edit, .allCommentCtn .commentSection .replyCard__footer .crudBtns .delete {
  transition: opacity 0.2s ease-in-out !important;
}
.allCommentCtn .commentSection .replyCard__footer .crudBtns .edit:hover, .allCommentCtn .commentSection .replyCard__footer .crudBtns .delete:hover {
  opacity: 0.5;
}
.allCommentCtn .replyBtn {
  transition: opacity 0.2s ease-in-out !important;
}
.allCommentCtn .replyBtn:hover {
  opacity: 0.5;
}
.allCommentCtn button {
  background-color: var(--mod-blue) !important;
  transition: opacity 0.2s ease-in-out !important;
}
.allCommentCtn button:hover {
  opacity: 0.5;
}
.allCommentCtn textarea {
  outline: 2px solid var(--light-grey);
  transition: outline 0.2s ease-in-out !important;
}
.allCommentCtn textarea:hover, .allCommentCtn textarea:focus {
  outline: 2px solid var(--mod-blue);
}

.addComment {
  width: min(92%, 46.875rem);
  margin-inline: auto;
}
.addComment .addCommentCard {
  width: 100%;
}
.addComment .addCommentCard textarea {
  outline: 2px solid var(--light-grey);
  transition: outline 0.2s ease-in-out !important;
}
.addComment .addCommentCard textarea:hover, .addComment .addCommentCard textarea:focus {
  outline: 2px solid var(--mod-blue);
}
.addComment .addCommentCard .footer button {
  background-color: var(--mod-blue);
  transition: opacity 0.2s ease-in-out !important;
}
.addComment .addCommentCard .footer button:hover {
  opacity: 0.5;
}

.replyTo {
  color: var(--mod-blue);
}

.overlay {
  height: 100vh !important;
  width: 100vw !important;
  inset: 0;
  opacity: 0.3;
}

.modal {
  width: min(95%, 23.75rem);
  height: auto;
  overflow: hidden;
  top: 50%;
  left: 50%;
  transform: translate(0%, -50%);
}
.modal p {
  color: var(--grey-blue);
}
.modal input {
  transition: outline-color 0.2s ease-in-out !important;
}
.modal input:focus {
  outline: 1px solid var(--blue);
}
.modal button {
  transition: opacity 0.2s ease-in-out !important;
  color: #fff;
  width: 45%;
}
.modal button:hover {
  opacity: 0.7;
}
.modal button.cancel {
  background-color: var(--grey-blue);
}
.modal button.yes {
  background-color: var(--soft-red);
}

.overlay,
.modal {
  display: none;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}
/* CSS for the element to be animated */
.hasFade {
  animation-duration: 0.3s;
  animation-fill-mode: both;
}

.fade-in {
  animation-name: fadeIn;
}

.fade-out {
  animation-name: fadeOut;
}

.overlay {
  opacity: 0.3 !important;
}

