  color: rgb(239 68 68/var(--tw-text-opacity));
}

.disabled\:pointer-events-none:disabled {
  pointer-events: none;
}

.disabled\:opacity-30:disabled {
  opacity: 0.3;
}

@media (min-width: 1024px) {
  .lg\:absolute {
    position: absolute;
  }
  .lg\:left-\[-3rem\] {
    left: -3rem;
  }
  .lg\:mt-4 {
    margin-top: 1rem;
  }
  .lg\:mt-1 {
    margin-top: 0.25rem;
  }
  .lg\:mb-0 {
    margin-bottom: 0px;
  }
  .lg\:block {
    display: block;
  }
  .lg\:w-\[88\%\] {
    width: 88%;
  }
  .lg\:w-\[70\%\] {
    width: 70%;
  }
  .lg\:w-\[75\%\] {
    width: 75%;
  }
  .lg\:flex-row {
    flex-direction: row;
  }
  .lg\:flex-col {
    flex-direction: column;
  }
  .lg\:justify-end {
    justify-content: flex-end;
  }
  .lg\:gap-x-5 {
    -moz-column-gap: 1.25rem;
    column-gap: 1.25rem;
  }
  .lg\:py-4 {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
  .lg\:px-3 {
    padding-left: 0.75rem;
    padding-right: 0.75rem;
  }
  .lg\:py-2 {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
  .lg\:pr-20 {
    padding-right: 5rem;
  }
  .lg\:pl-24 {
    padding-left: 6rem;
  }
  .lg\:pb-4 {
    padding-bottom: 1rem;
  }
  .lg\:pr-4 {
    padding-right: 1rem;
  }
  .lg\:pb-6 {
    padding-bottom: 1.5rem;
  }
  .lg\:pt-4 {
    padding-top: 1rem;
  }
  .lg\:text-right {
    text-align: right;
  }
}